<template>
  <div>
    <div class="gas-wrap">
      <div id="coldMap"></div>
      <div class="gasMapInfo-wrap">
        <div class="gas-all cold"><span class="icon-cold"></span> 8</div>
        <div class="station-info">
          <div>
            <el-row class="border-bottom">
              <el-col :span="10"><div class="grid-content bg-purple border-right">
                <el-select v-model="districtValue" placeholder="" size="mini">
                  <el-option
                    v-for="item in districtOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </div></el-col>
              <el-col :span="14"><div class="grid-content bg-purple-light">
                <el-select v-model="stationValue" placeholder="" size="mini">
                  <el-option
                    v-for="item in stationOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </div></el-col>
            </el-row>
            <div class="grid-wrap">
              <el-row :gutter="10">
                <el-col :span="24"><div class="grid-content bg-purple">
                  <ul>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">4164</span> kM</p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">439</span> </p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">3200</span> kM</p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">5080</span> kM</p></li>
                  </ul>
                </div></el-col>
              </el-row>
              <el-row :gutter="10">
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">111</p>
                  <p class="c-text"></p>
                </div></el-col>
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">323</p>
                  <p class="c-text"></p>
                </div></el-col>
              </el-row>
              <el-row :gutter="10">
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">454</p>
                  <p class="c-text"></p>
                </div></el-col>
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">321</p>
                  <p class="c-text"></p>
                </div></el-col>
              </el-row>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        districtOption: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        stationOption:[{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        districtValue: '',
        stationValue:''
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



