<template>
  <div>
    <div class="left">
      <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
    </div>
    <div class="left chart-wrap">
      <div class="gis-wrap">
        <div id="powerMap"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        treeData: [],
        checkList: []
      }
    },
    mounted(){
      let that = this;
      that.getPVTree();
    },
    methods: {
      PVviewById:function(id){

      },
      handleClick(tab, event) {
      },
      getPVTree(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/cost/getPVTree", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.getPVTreeData(data);
            }
          });
      },
      getPVTreeData(data){
        if(data){
          let that = this;
          let xData = [];
          let seriesData = [];
          that.treeData = data;
        }
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

