<template>
  <div>
    <div class="gas-wrap">
      <div id="coldMap"></div>
      <div class="gasMapInfo-wrap">
        <div class="gas-all heat"><span class="icon-heat"></span> 8</div>
        <div class="station-info">
          <div>
            <el-row class="border-bottom">
              <el-col :span="10"><div class="grid-content bg-purple border-right">
                <el-select v-model="districtValue" placeholder="" size="mini">
                  <el-option
                    v-for="item in districtOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </div></el-col>
              <el-col :span="14"><div class="grid-content bg-purple-light">
                <el-select v-model="stationValue" placeholder="" size="mini">
                  <el-option
                    v-for="item in stationOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </div></el-col>
            </el-row>
            <div class="grid-wrap">
              <el-row :gutter="10">
                <el-col :span="24"><div class="grid-content bg-purple">
                  <ul>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">4164</span> kM</p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">439</span> </p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">3200</span> kM</p></li>
                    <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">5080</span> kM</p></li>
                  </ul>
                </div></el-col>
              </el-row>
              <el-row :gutter="10">
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">111</p>
                  <p class="c-text"></p>
                </div></el-col>
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">323</p>
                  <p class="c-text"></p>
                </div></el-col>
              </el-row>
              <el-row :gutter="10">
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">454</p>
                  <p class="c-text"></p>
                </div></el-col>
                <el-col :span="12"><div class="grid-content bg-purple">
                  <p class="c-val">321</p>
                  <p class="c-text"></p>
                </div></el-col>
              </el-row>
              <div id="heatSource"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  export default {
    data () {
      return {
        districtOption: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        stationOption:[{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        districtValue: '',
        stationValue:''
      }
    },
    mounted(){
      let that = this;
      that.getheatSourceChart();
    },
    methods:{
      getheatSourceChart(data) {
        var that = this;
        // domecharts
        var myChart = echarts.init(document.getElementById("heatSource"));
        // 
        var option = {
          color:['#f2627b','#975fe6','#006eeb','#2ebd41','#f7bf48'],
          tooltip: {
            trigger: 'item',
            formatter: "{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            top:20,
            right:0,
            align:'left',
            data:[{
              name:'',
              icon:'circle'
            },{
              name:'',
              icon:'circle'
            },{
              name:'',
              icon:'circle'
            },{
              name:'',
              icon:'circle'
            },{
              name:'',
              icon:'circle'
            }]
          },
          series: [
            {
              name:'',
              type:'pie',
              center: ['22%', '55%'],
              radius: ['40%', '60%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '12',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:[
                {value:335, name:''},
                {value:679, name:''},
                {value:1548, name:''},
                {value:1548, name:''},
                {value:1548, name:''}
              ]
            }
          ]
        };
        // 
        myChart.setOption(option);
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



