        getuseRatioChart(){
          var myChart = echarts.init(document.getElementById('useRatioChart'));
          var option = {
            tooltip : {
              formatter: "{a} <br/>{b} : {c}%"
            },
            series: [
              {
                name: '',
                type: 'gauge',
                min:0,
                max:100,
                splitNumber:5,
                radius:'100%',
                startAngle:200,
                endAngle:-380,
                axisLine: {
                  lineStyle: {
                    width: 7,
                    color: [
                      [1, '#096eda']
                    ],
                  }
                },
                axisTick:{
                  show:false,
                  splitNumber:5
                },
                axisLabel:{
                  color:'#a6a6a4',
                  distance:5,
                  fontSize:10
                },
                splitLine:{
                  show:true,
                  length:5
                },
                itemStyle:{
                  color:'#fe7235'
                },
                title:{
                  color:'#979797',
                  fontSize:14,
                  offsetCenter:[0,'70%']
                },
