<template>
    <div>
      <div class="tab-wrap hidden">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="GIS" name="gis"></el-tab-pane>
          <el-tab-pane label="" name="power"></el-tab-pane>
          <el-tab-pane label="" name="gas"></el-tab-pane>
          <el-tab-pane label="" name="heat"></el-tab-pane>
          <el-tab-pane label="" name="cold"></el-tab-pane>
        </el-tabs>
        <div class="btn-right">
          <el-button size="mini" type="primary" @click.native="overViewEvent" plain></el-button>
          <el-button size="mini" type="primary" @click.native="kpiEvent" plain>KPI</el-button>
        </div>
      </div>
      <vGis v-if="showgis"></vGis>
      <vPower v-if="showpower"></vPower>
      <vGas v-if="showgas"></vGas>
      <vHeat v-if="showheat"></vHeat>
      <vCold v-if="showcold"></vCold>
      <vOverView v-if="showoverView"></vOverView>
      <vKpi v-if="showkpi"></vKpi>
    </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
  import vGis from "./gis.vue";
  import vPower from "./power.vue";
  import vGas from "./gas.vue";
  import vHeat from "./heat.vue";
  import vCold from "./cold.vue";
  import vOverView from "./overView.vue";
  import vKpi from "./kpi.vue";
    export default {
        data () {
            return {
              activeName:'gis',
              treeData: [],
              showgis:true,
              showpower:false,
              showgas:false,
              showheat:false,
              showcold:false,
              showoverView:false,
              showkpi:false
            }
        },
      mounted(){
        let that = this;
      },
      methods: {
        PVviewById:function(id){

        },
        handleClick(tab, event) {
          var that = this;
          if(tab.paneName == 'gis'){
            that.showgis = true;
            that.showpower = false;
            that.showgas = false;
            that.showheat = false;
            that.showcold = false;
            that.showoverView = false;
            that.showkpi = false;
          }else if(tab.paneName == 'power'){
            that.showgis = false;
            that.showpower = true;
            that.showgas = false;
            that.showheat = false;
            that.showcold = false;
            that.showoverView = false;
            that.showkpi = false;
          }else if(tab.paneName == 'gas'){
            that.showgis = false;
            that.showpower = false;
            that.showgas = true;
            that.showheat = false;
            that.showcold = false;
            that.showoverView = false;
            that.showkpi = false;
          }else if(tab.paneName == 'heat'){
            that.showgis = false;
            that.showpower = false;
            that.showgas = false;
            that.showheat = true;
            that.showcold = false;
            that.showoverView = false;
            that.showkpi = false;
          }else if(tab.paneName == 'cold'){
            that.showgis = false;
            that.showpower = false;
            that.showgas = false;
            that.showheat = false;
            that.showcold = true;
            that.showoverView = false;
            that.showkpi = false;
          }
        },
        overViewEvent(){
          var that = this;
          that.showgis = false;
          that.showpower = false;
          that.showgas = false;
          that.showheat = false;
          that.showcold = false;
          that.showoverView = true;
          that.showkpi = false;
          that.activeName = '';
        },
        kpiEvent(){
          var that = this;
          that.showgis = false;
          that.showpower = false;
          that.showgas = false;
          that.showheat = false;
          that.showcold = false;
          that.showoverView = false;
          that.showkpi = true;
          that.activeName = '';
        }
      },
      components: {
        TreeOrg,vGis,vPower,
        vGas,
        vHeat,
        vCold,
        vOverView,
        vKpi
      },
    }
</script>

