<template>
  <div>
    <div class="left">
      <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
    </div>
    <div class="left chart-wrap">
      <div class="gis-wrap">
        <div id="gisMap"></div>
        <div class="mapInfo-wrap">
          <div class="energy-type">
            <el-row :gutter="10">
              <el-col :span="8"><div class="grid-content bg-purple">
                <span class="icon-img power"></span>
                <span class="icon-name"></span>
              </div></el-col>
              <el-col :span="8"><div class="grid-content bg-purple">
                <span class="icon-img HC"></span>
                <span class="icon-name">/</span>
              </div></el-col>
              <el-col :span="8"><div class="grid-content bg-purple">
                <span class="icon-img gas"></span>
                <span class="icon-name"></span>
              </div></el-col>
            </el-row>
          </div>
          <div class="map-type">
            <el-checkbox-group v-model="checkList">
              <el-checkbox label="()"></el-checkbox>
              <el-checkbox label="(/)"></el-checkbox>
              <el-checkbox label="()"></el-checkbox>
              <el-checkbox label=""></el-checkbox>
              <el-checkbox label=""></el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        treeData: [],
        checkList: []
      }
    },
    mounted(){
      let that = this;
      that.getPVTree();
    },
    methods: {
      PVviewById:function(id){

      },
      handleClick(tab, event) {
      },
      getPVTree(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/cost/getPVTree", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.getPVTreeData(data);
            }
          });
      },
      getPVTreeData(data){
        if(data){
          let that = this;
          let xData = [];
          let seriesData = [];
          that.treeData = data;
        }
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



