<template>
    <div>
      <div class="gas-wrap">
        <div id="gasMap"></div>
        <div class="gasMapInfo-wrap">
          <div class="gas-all"><span class="icon-station"></span> 8</div>
          <div class="station-info">
            <div>
              <el-row class="border-bottom">
                <el-col :span="10"><div class="grid-content bg-purple border-right">
                  <el-select v-model="districtValue" placeholder="" size="mini">
                    <el-option
                      v-for="item in districtOption"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </div></el-col>
                <el-col :span="14"><div class="grid-content bg-purple-light">
                  <el-select v-model="stationValue" placeholder="" size="mini">
                    <el-option
                      v-for="item in stationOption"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </div></el-col>
              </el-row>
              <div class="grid-wrap">
                <el-row :gutter="10">
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="s-val">1765</p>
                    <p class="s-text">(m)</p>
                  </div></el-col>
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="s-val">8</p>
                    <p class="s-text">()</p>
                  </div></el-col>
                </el-row>
                <el-row :gutter="10">
                  <el-col :span="24"><div class="grid-content bg-purple">
                    <ul>
                      <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">0.4164</span> MPa</p></li>
                      <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">39.58</span> </p></li>
                      <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">0.0</span> M/h</p></li>
                      <li class="hidden flow"><label class="s-text"></label><p class="right s-text"><span class="blue-val">32933</span> M</p></li>
                    </ul>
                  </div></el-col>
                </el-row>
                <el-row :gutter="10">
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">45</p>
                    <p class="c-text"></p>
                  </div></el-col>
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">20</p>
                    <p class="c-text"></p>
                  </div></el-col>
                </el-row>
                <el-row :gutter="10">
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">10</p>
                    <p class="c-text"></p>
                  </div></el-col>
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">10</p>
                    <p class="c-text"></p>
                  </div></el-col>
                </el-row>
                <el-row :gutter="10">
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">30</p>
                    <p class="c-text"></p>
                  </div></el-col>
                  <el-col :span="12"><div class="grid-content bg-purple">
                    <p class="c-val">120</p>
                    <p class="c-text"></p>
                  </div></el-col>
                </el-row>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import ol from "openlayers";
  import icon_station from "../../../../assets/img/loc.png";
  var map, stationLayer; //
  var ol_center_ll = [12635198.426914247, 2543848.1879067607];
    export default {
        data () {
            return {
              districtOption: [{
                value: '1',
                label: ''
              }, {
                value: '2',
                label: ''
              }, {
                value: '3',
                label: ''
              }, {
                value: '4',
                label: ''
              }, {
                value: '5',
                label: ''
              }],
              stationOption:[{
                value: '1',
                label: ''
              }, {
                value: '2',
                label: ''
              }, {
                value: '3',
                label: ''
              }, {
                value: '4',
                label: ''
              }, {
                value: '5',
                label: ''
              }],
              districtValue: '',
              stationValue:''
            }
        },
      mounted(){
        let that = this;
        that.initMap();
      },
      methods:{
        initMap() {
          //
          var that = this;
          map = new ol.Map({
            target: "gasMap",
            layers: [
              new ol.layer.Tile({
                source: new ol.source.OSM()
              })
            ],
            view: new ol.View({
              maxZoom: 22,
              minZoom: 1,
              zoomFactor: 2,
              center: ol_center_ll,
              zoom: 12
            })
          });

          stationLayer = new ol.layer.Vector({
            //
            source: new ol.source.Vector(),
            zIndex: 12,
            style: function(feature) {
              var coordinates = feature.getGeometry().getCoordinates();
              var styles = new ol.style.Style({
                geometry: new ol.geom.Point(coordinates),
                image: new ol.style.Icon({
                  src: icon_station,
                  anchorXUnits: "pixels",
                  anchorYUnits: "pixels",
                  anchor: [10, 10]
                })
              });
              return styles;
            }
          });
          map.addLayer(stationLayer);
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



