                icon:'circle'
              },{
                name:'',
                icon:'circle'
              },{
                name:'',
                icon:'circle'
              }],
              formatter:  function(name){
                var total = 0;
                var target;
                for (var i = 0, l = data.length; i < l; i++) {
                  total += data[i].value;
                  if (data[i].name == name) {
                    target = data[i].value;
                  }
                }
                var arr = [
                  '{b|'+name+'} | {a|'+((target/total)*100).toFixed(2)+'%}',
                ]
                // return name + ' ' + ((target/total)*100).toFixed(2) + '%';
                return arr
              },
              textStyle:{
                rich:{
                  a:{
                    fontSize:14,
                    //verticalAlign:'top',
                    //align:'center',
                    padding:[0,0,5,10]
                  },
                  b:{
                    fontSize:14,
                    //align:'center',
                    padding:[0,10,0,0],
                    //lineHeight:25
                  }
                }
              }
            },
            series: [
              {
                name:'',
                type:'pie',
                center: ['25%', '50%'],
