                  color: "#e6e8ed"
                }
              }
            },
            series: [
              {
                name: "",
                type: "line",
                symbol: 'none',
                stack: "",
                data:powerData,
              },
              {
                name: "",
                type: "line",
                symbol: 'none',
                stack: "",
                data:gasData,
              },
              {
                name: "",
                type: "line",
                symbol: 'none',
                stack: "",
                data:heatData,
              }
            ]
          };
          // 
          myChart.setOption(option);
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #allEnergyRatio,#cleanEnergyRatio,#cleanEnergyReduce{
    height: 60px;
  }
  #cleanRatio,#energyRatio,#energyChangeRatio{
    height: 220px;
  }
